.card {
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  width: 200px;
  height: 408px;
  padding: 33px 17px 17px;
  font-family: Roboto, sans-serif;
  display: flex;
}

.card__picture {
  align-self: center;
  width: 160px;
}

.card__title {
  color: #060b35;
  margin-top: 40px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  color: #616070;
  margin-top: 4px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__score {
  margin-top: 16px;
}

.card__price {
  margin-top: 24px;
}

.card__buy {
  color: #fff;
  text-transform: uppercase;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 40px;
  margin-top: 16px;
  font-size: 14px;
  font-weight: 700;
  text-decoration: none;
  display: flex;
}

.card__buy:hover {
  color: #00acdc;
  background-color: #fff;
  transition: background-color .5s, color .5s;
}

.card:hover {
  transition: transform .5s;
  transform: scale(1.2);
}

.card:hover .card__title {
  color: #34568b;
}

.stars {
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.stars__stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: 50%;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__stars--0 .stars__star:nth-child(-n), .stars__stars--1 .stars__star:nth-child(-n+1), .stars__stars--2 .stars__star:nth-child(-n+2), .stars__stars--3 .stars__star:nth-child(-n+3), .stars__stars--4 .stars__star:nth-child(-n+4), .stars__stars--5 .stars__star:nth-child(-n+5) {
  background-image: url("star-active.edda4c43.svg");
}

.stars__reviews {
  color: #060b35;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.price {
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  display: flex;
}

.price__title {
  color: #616070;
  font-size: 12px;
  font-weight: 400;
  line-height: 14px;
}

.price__amount {
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.header {
  text-transform: uppercase;
  justify-content: space-between;
  align-items: center;
  margin: auto;
  padding: 0 50px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header__logo {
  width: 40px;
  height: 40px;
  display: flex;
}

.nav {
  display: flex;
}

.nav__link {
  color: #060b35;
  align-items: center;
  height: 60px;
  text-decoration: none;
  display: flex;
}

.nav__link--active {
  color: #00acdc;
  position: relative;
}

.nav__link--active:after {
  content: "";
  background: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
}

.nav__list {
  align-items: center;
  padding: 0;
  display: flex;
}

.nav__list li:last-child {
  margin-right: 0;
}

.nav__item {
  text-transform: uppercase;
  align-items: center;
  height: 100%;
  margin-right: 20px;
  list-style: none;
  display: flex;
}

.nav__link:hover {
  color: #00acdc;
  transition: color .5s;
}

.products {
  grid-template-columns: repeat(auto-fill, 200px);
  justify-content: center;
  place-items: center;
  gap: 48px 46px;
  max-width: 1055px;
  margin: 0 auto;
  padding: 50px 40px;
  display: grid;
}

* {
  box-sizing: border-box;
  margin: 0;
}

